@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.pdf-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to right, #0e6a98 20%, #014483 30% 80%, #0e6a98 90%);
}

.react-pdf__Page__canvas {
    /* width: 100vw!important;
    min-width: 1000px!important; */
}

.react-pdf__Document {
    box-shadow: white 1px 1px 10px;
}
.reports-container {
    width: 100vw;
    height: auto;
    min-height: 100vh;
    display: flex;
    flex-flow: wrap;
    /* flex-direction: column; */
    justify-content: center;
    /* align-items: center; */
    align-items: flex-start;
    /* background-image: linear-gradient(to right, #0e6a98 20%, #014483 30% 100%, #0e6a98 80%); */
    background-image: linear-gradient(#011e41, #014483);
    /* background-color: #014483; */
    z-index: 1;
}

.solo-report {
    align-items: center;
}

.footer-container {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: darkblue;
    background-image: linear-gradient(to top, #011e41, #014483);
    z-index: 2;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    font-family: Roboto;
}

.reports-header {
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: white;
    font-size: 1.8rem;
    /* border: solid 1px white; */
    width: 90%;
    font-family: Roboto;
    text-align: center;
}

.report-container {
    position: relative;
    --width: 40vw;
    --height: calc(var(--width) * 0.56);
    width: var(--width);
    margin: 0 1rem 3rem 1rem;
    /* margin-top: 3rem; */
    /* height: var(--height); */
    z-index: 1;
    /* box-shadow: whitesmoke 0px 0px 7px 1px; */
    /* border: solid white 1px; */
    /* border-radius: 0px 0px 4px 4px; */
    /* border-top: none; */
    /* padding-bottom: 2px; */
    /* max-width: 530px; */
}

.privacy-cta-container {
    font-family: Roboto;
    /* position: absolute;
    bottom: 10px;
    left: calc(50% - 100px); */
    width: 200px;
    color: lightgrey;
}

.ctas-container {
    font-family: Roboto;
    /* position: absolute;
    right: 10px;
    bottom: 5px; */
    color: white;
}

.cta-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.cta-img {
    margin-left: .2rem;
    /* transform: scaleY(.7); */
    max-width: 100px;
}

.cta {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    text-decoration: none;
    /* width: 100%; */
    font-weight: 600;
}

.cta-txt {
    font-weight: 400;
    font-size: 1.2rem;
    color: white;
    width: 100%;
}

.cta:hover {
    -webkit-transform: scaleX(1.03);
            transform: scaleX(1.03);
    font-weight: 700;
}

.reports-header-container {
    /* margin-top: 3rem; */
    padding: .7rem;
    /* width: calc(50% - 1.4rem); */
    color: white;
    font-family: Roboto;
    /* max-height: 200px; */
    /* box-shadow: whitesmoke 0px 0px 7px 1px; */
    /* border: solid white 1px;
    border-radius: 4px 4px 0px 0px;
    border-bottom: none; */
    /* max-width: 530px; */
}

.report-signature {
    margin-top: 1rem;
    font-size: 1.2rem;
}

.report-header {
    font-size: 1.5rem;
}

.report-link {
    height: var(--height);
}

/* .report-container:hover {
    box-shadow: whitesmoke 0px 0px 10px 3px;
} */

.report-cover {
    width: var(--width);
    /* max-width: 530px; */
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}

.report-details-container {
    position: relative;
    display: flex;
    justify-content: center;
    background-color: whitesmoke;
    transition: all ease-in-out 500ms;
    z-index: -1;
}

.hidden {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}

@media(max-width: 850px) {
    .report-container {
        width: 75vw;
    }

    .report-cover {
        width: 75vw;
    }

    .ctas-container {
        bottom: 15px;
    }

    .reports-header {
        font-size: 1.3rem;
    }

    .report-header {
        font-size: 1.1rem;
    }

    .report-signature {
        font-size: 1.1rem;
    }
}


.privacy-container-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.privacy-container {
    width: 80%;
    /* padding: 1rem; */
    margin: 1rem;
    margin-bottom: 1rem;
}

.main-header {
    font-weight: 600;
    font-size: 2rem;
    width: 100%;
    text-align: center;
}

.section-header {
    font-weight: 600;
    font-size: 1.3rem;
}

.paragraph {
    margin: 1rem;
}
.fade-in {
  position: relative;
  color: antiquewhite;
  font-size: 1.7rem;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 300;
  opacity: 0;
  transition: opacity 1000ms ease-in-out;
}

.fade-in.appear {
  opacity: 1;
}

#take-survey {
  position: relative;
  top: 50%;
  background-color: rgb(25, 28, 54);
  box-shadow: #0d6efd 0px 0px 1000px 10px;
  transition: box-shadow 2500ms ease-in;
  margin-top: 5rem;
}

#div-sentence {
  display: flex;
  justify-content: space-between;
}

.paragraph-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.paragraph {
    width: 90%;
    display: flex;
    flex-flow: column;
}

.paragraph > div {
    margin: 1rem;
}

.button-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.button {
    border: white solid 1px;
    width: 150px;
    text-align: center;
    transition: all 500ms;
}

.button:hover {
    cursor: pointer;
    /* width: 200px; */
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

iframe {
  position: absolute;
  height: 100%;
  width: 100%;
  left:0;
  right:0;
  bottom:0;
  top:0;
  border:0;
}

.frame {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  /* position: absolute; */
  background-color: rgb(25, 28, 54);
  /* height: 3000px; */
  width: 100%;
}

.frame > img {
  height: 100%;
  width: 100%;
}

.link {
  text-decoration: none;
  color: inherit;
}

.link:visited {
  text-decoration: none;
}

@-webkit-keyframes move {
  from {
    offset-distance: 0%;
  }

  to {
    offset-distance: 100%;
  }
}

@keyframes move {
  from {
    offset-distance: 0%;
  }

  to {
    offset-distance: 100%;
  }
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

